<template>
    <div class="page">
        <!-- <div class="marquee">
            <van-notice-bar left-icon="volume-o" text="Hello PCD" />
        </div> -->
        <div class="pages">
            <!--  -->
            <div class="nav">
                <div class="logo"><img src="./img/logo.png" alt="" srcset=""></div>
                <div class="navList">
                    <a href="" class="isShow">DAO</a>
                    <a href="#" @click="navFlag = true"><svg t="1714794810902" class="icon" viewBox="0 0 1024 1024"
                            version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="12817" width="200" height="200">
                            <path
                                d="M127.9 168.9h768v46h-768zM127.2 809.1h768v46h-768zM408.1 495.5h488.7v46H408.1zM317.6 346.3L130.1 522.6l187.5 176.3V346.3z m-46 246.2l-74.3-69.9 74.3-69.9v139.8z"
                                p-id="12818" fill="#ffffff"></path>
                        </svg></a>
                </div>

            </div>
            <div class="navB" v-show="navFlag">
                <div class="navTwo">
                    <div class="logo"><img src="./img/logo.png" alt="" srcset=""></div>
                    <div class="navList">
                        <a href="#" @click="navFlag = false"><svg t="1714798443381" class="icon" viewBox="0 0 1024 1024"
                                version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2691" width="200" height="200">
                                <path
                                    d="M760.96 195.2L512 444.16 263.04 195.2a48 48 0 0 0-67.84 67.84L444.16 512l-248.96 248.96A48 48 0 0 0 263.04 832L512 579.84 760.96 832A48 48 0 0 0 832 760.96L579.84 512 832 263.04a48 48 0 0 0-67.84-67.84z"
                                    p-id="2692" fill="#ffffff"></path>
                            </svg></a>
                    </div>

                </div>
                <div class="navMax">
                    <a href="">Index</a>
                    <!-- <a href="https://www.pcddao.com/">Web Site</a>
                    <a href="https://www.pcddao.com/#/Labs">Labs</a>
                    <a href="https://docs.pcddao.com/">Docs</a>
                    <a href="https://www.pcddao.com/#/Bridge">Bridge</a>
                    <a href="https://swap.pcddao.com/">Swap</a> -->
                </div>
            </div>

            <!-- 增加一个公告区域 -->
            <div class="notice">
                <div class="notice_title">
                    <span class="gradient-text">Announcement</span>
                </div>
                <div class="notice_content">
                    <div class="notice_item">
                        <h3 class="notice_item_title gradient-text">PCD Public Chain Node Voting & Staking Model Upgrade
                        </h3>
                        <div class="notice_item_text">
                            <p class="main-desc">The node system is being optimized to enhance data security and ensure
                                sustainable market operations. The updated node voting and staking model will be
                                deployed on the Binance Chain.</p>

                            <h4 class="section-title gradient-text">Migration Process & Fees:</h4>
                            <ul class="migration-list">
                                <li>A 20 PCD fee will be deducted to facilitate the migration.</li>
                                <li>After the migration, users will be able to withdraw their tokens directly from the
                                    Binance Chain.</li>
                                <li>To minimize migration costs, PCD tokens in the public chain wallet can be
                                    consolidated into a single address.</li>
                                <li>Tokens associated with nodes, voting, and release contracts will be available for
                                    withdrawal in the corresponding wallets post-migration.</li>
                            </ul>

                            <p class="timeline"><strong class="gradient-text">Upgrade Timeline:</strong> The migration
                                is expected to take place between March 8–10.</p>
                            <p class="timeline"><strong class="gradient-text">Data Migration:</strong> Expected to
                                complete by March 13.</p>

                            <p class="notice_footer">For a detailed breakdown and further inquiries, please join the
                                PCDChain group on the telegram or BTOK app.</p>
                        </div>
                    </div>
                </div>
            </div>

        </div>
        <van-popup v-model="lockPup">
            <div class="popUpBox">
                <div class="popUpBox_title">Lock</div>
                <div class="popUpBox_input">
                    <input type="number" v-model="amount" placeholder="Lock Num">
                    <div class="popUpBox_input_right">
                        <div class="popUpBox_right_A">PCD</div>
                        <div class="popUpBox_right_B"></div>
                        <div class="popUpBox_right_C" @click="allTFX">All</div>
                        <div class="popUpBox_right_D">
                            <div class="add"><img @click="amount++" src="./img/add.png" alt="" srcset=""></div>
                            <div class="reduce"><img @click="amount--" src="./img/reduce.png" alt="" srcset=""></div>
                        </div>
                    </div>
                </div>
                <div class="popUpBox_p">*The quantity of staking must be a multiple of 100.</div>
                <div class="popUpBox_p">*When adding to the position, the interest will be automatically collected into
                    the
                    balance.</div>
                <div class="popUpBox_btn">
                    <span class="popUpBox_btnCancel" @click="showLock(false)">Cancel</span>
                    <span class="popUpBox_btnOk" @click="lock">OK</span>
                </div>
            </div>
        </van-popup>

        <van-popup v-model="takePup">
            <div class="popUpBox">
                <div class="popUpBox_title">take</div>
                <div class="popUpBox_input">
                    <input type="number" v-model="amount" placeholder="take Num">
                    <div class="popUpBox_input_right">
                        <div class="popUpBox_right_A">PCD</div>
                        <div class="popUpBox_right_B"></div>
                        <div class="popUpBox_right_C" @click="allBlance()">All</div>
                        <div class="popUpBox_right_D">
                            <div class="add"><img @click="amount++" src="./img/add.png" alt="" srcset=""></div>
                            <div class="reduce"><img @click="amount--" src="./img/reduce.png" alt="" srcset=""></div>
                        </div>
                    </div>
                </div>
                <div class="popUpBox_p">* The withdrawal amount will be transferred directly to the wallet
                    address.</div>
                <div class="popUpBox_btn">
                    <span class="popUpBox_btnCancel" @click="showTake(false)">Cancel</span>
                    <span class="popUpBox_btnOk" @click="take()">OK</span>
                </div>
            </div>
        </van-popup>

        <van-popup v-model="relockPup">
            <div class="popUpBox">
                <div class="popUpBox_title">relock</div>
                <div class="popUpBox_input">
                    <input type="number" v-model="amount" placeholder="Relock Num">
                    <div class="popUpBox_input_right">
                        <div class="popUpBox_right_A">PCD</div>
                        <div class="popUpBox_right_B"></div>
                        <div class="popUpBox_right_C" @click="allBlance()">All</div>
                        <div class="popUpBox_right_D">
                            <div class="add"><img @click="amount++" src="./img/add.png" alt="" srcset=""></div>
                            <div class="reduce"><img @click="amount--" src="./img/reduce.png" alt="" srcset=""></div>
                        </div>
                    </div>
                </div>
                <div class="popUpBox_btn">
                    <span class="popUpBox_btnCancel" @click="showRelock(false)">Cancel</span>
                    <span class="popUpBox_btnOk" @click="relock()">OK</span>
                </div>
            </div>
        </van-popup>

        <van-popup v-model="unlockPup">
            <div class="popUpBox">
                <div class="popUpBox_title">Unlock</div>
                <div class="popUpBox_input">
                    <input type="number" v-model="amount" placeholder="Lock Num">
                    <div class="popUpBox_input_right">
                        <div class="popUpBox_right_A">PCD</div>
                        <div class="popUpBox_right_B"></div>
                        <div class="popUpBox_right_C" @click="allTFX">All</div>
                        <div class="popUpBox_right_D">
                            <div class="add"><img @click="amount++" src="./img/add.png" alt="" srcset=""></div>
                            <div class="reduce"><img @click="amount--" src="./img/reduce.png" alt="" srcset=""></div>
                        </div>
                    </div>
                </div>
                <div class="popUpBox_p">Transaction fee:&nbsp;20%</div>
                <div class="popUpBox_p">Estimated arrival:&nbsp;{{ (amount * 0.8).toFixed(5) }}</div>
                <div class="popUpBox_p">* The withdrawal amount will be transferred directly to the wallet
                    address.</div>
                <div class="popUpBox_btn">
                    <span class="popUpBox_btnCancel" @click="showUnlock(false)">Cancel</span>
                    <span class="popUpBox_btnOk" @click="unlock">OK</span>
                </div>
            </div>
        </van-popup>
    </div>

</template>

<script>
import {
    h5Copy,
    initEth,
    timeUtils,
    vertify,
    Decimal
} from "@/utils/utils";

import {
    ethers
} from "ethers";

import {
    abi
} from "@/utils/abi";
import {
    tokenAbi
} from "@/utils/token";
import {
    Toast
} from "vant";
import QRCode from 'qrcodejs2';

export default {
    components: {},
    data() {
        return {
            navFlag: false,
            marqueeContent: '',
            isshowQO: false,
            arr: "a",
            falg: false,
            num: "",
            data: this.$t('ttx'),
            placeholder: this.$t('Please enter your inviter\'s address'),
            message: this.$t("Please enter the address of the binding invitation"),
            replication: "Successful replication",
            languagefalgfu: false,
            languagefalg: false,
            HomeFlag: false,
            ExplorerFlag: false,
            TokenFlag: false,
            ChainFlag: false,
            TestFlag: false,
            languageList: ['a', 'b', 'c', 'd', 'e'],
            contractAddress: "0xDa28C66cE9283D2E07FC4439C828bE9972B88965", // 合约地址
            tokenContractAddress: "0xCEb24C99579e6140517D59c8Dd4F5b36D84ED6de", //质押的token合约地址
            contract: null, // 当前的合约对象
            tokenContract: null, // 质押的token合约对象
            myAddress: "", // 我的地址
            balance: 0, // 我的余额
            interest: 0,
            alllock: 0,
            userUnlock: 0,
            star: 0,
            min_gasprice: 1, // 最小gas价格
            power: 0, // 我的算力
            //团队算力
            level: 0,
            bgShow: false,
            type: 1,
            inviteCount: 0, // 邀请的人数
            inviteAddress: "", // 已绑定邀请人地址
            inviteAddressInput: "", // 输入邀请人的地址
            amount: 0, // 燃烧数量
            decimals: 18, //精度
            urlAddress: "",
            lqShow: false,
            allowance: 0,
            constants: {},
            text: "",
            userBalance: 0,
            lockPup: false,
            takePup: false,
            relockPup: false,
            showEnAndCh: false,
            teamPowers: 0,
            chacklevel: 0,
            qrcode: null,
            ipAddress: '',// 存放IP地址的变量
            isshowQOpeo: false,
            unlockPup: false,
            flagType: "",
            queryParams: [],
            linkUrl: window.location.protocol + '//' + window.location.hostname + '/#/dapp?address=',
        }
    },

    created() {
        this.init();
    },
    mixins: [h5Copy, initEth, timeUtils, vertify, Decimal],
    mounted() {
        window.addEventListener('scroll', this.handleScroll)
    },
    methods: {
        binding() {

        },
        isshowQOs() {
            this.isshowQO = true
            this.$nextTick(() => {
                this.bindQRCode()
            })

        },

        bindQRCode() {
            if (this.qrcode) {
                // 如果二维码对象已存在，则更新链接
                this.qrcode.clear(); // 清除之前的二维码
                this.qrcode.makeCode(url); // 生成新的二维码
            } else {
                this.qrcode = new QRCode(this.$refs.qrCodeDiv, {
                    text: window.location.href + '?inviter=' + this.myAddress,
                    width: 200,
                    height: 200,
                    colorDark: "#333333", //二维码颜色
                    colorLight: "#ffffff", //二维码背景色
                    correctLevel: QRCode.CorrectLevel.L //容错率，L/M/H
                })
            }


        },
        handleScroll() {

            this.$nextTick(() => {
                var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body
                    .scrollTop
                if (scrollTop > 0) {
                    document.getElementById('scrollTopBg').style.backgroundColor = '#02088B'
                } else {
                    document.getElementById('scrollTopBg').style.backgroundColor = 'rgba(2, 8, 139, 0)'
                }
            });
            this.$forceUpdate()
        },
        promptDialogBox() {

            this.$message({
                message: this.message,
                type: 'success',
                iconClass: ' ',
            });
        },
        index(i) {
            if (i == 1) {
                this.$router.push({
                    path: '/'
                })
            } else {
                this.$router.push({
                    path: '/dapp'
                })
            }
        },
        clicks(e) {
            console.log(e)
            this.arr = e
            if (this.arr == 'a') {
                this.$i18n.locale = 'en'
            } else if (this.arr == 'b') {
                this.$i18n.locale = 'zh-CN'
            } else if (this.arr == 'c') {
                this.$i18n.locale = 'Korean'
            } else if (this.arr == 'd') {
                this.$i18n.locale = 'Russian'
            } else if (this.arr == 'e') {
                this.$i18n.locale = 'Japanese'
            }
            console.log(this.$i18n.locale)
            this.languagefalgfu = false
            this.data = this.$t('ttx')
            this.languagefalg = false
            localStorage.setItem('languageConverter', this.$i18n.locale)
            this.$forceUpdate()
        },
        clickTo() {
            this.$i18n.locale = 'en'
        },
        cli() {
            this.falg = !this.falg
        },

        async init() {
            if (this.signer == null) {
                return;
            }
            let _gasPrice = await this.provider.getGasPrice();
            _gasPrice = ethers.utils.formatUnits(_gasPrice, "gwei");
            console.log("GAS:" + _gasPrice);
            if (_gasPrice > this.min_gasprice) this.min_gasprice = _gasPrice; //如果网络当前矿工费高于预设最小值，使用当前值
            var contract = new ethers.Contract(
                this.contractAddress,
                abi,
                this.signer
            );
            this.contract = contract;

            setInterval(this.timer, 10000);
            //获取我的算力
            this.getPower();
            this.getUserBalance();
            this.getUserUnlock();
            //获取用户等级
            this.getInterest();
            // this.getinviteCount(); //获取我的邀请人数
            // this.getInviteAddress(); //获取我的邀请地址
            let {
                address
            } = this.$route.query;
            if (address) {
                this.urlAddress = address;
                console.log(this.urlAddress)
                if (this.inviteAddress == "") {
                    this.inviteAddressInput = address;
                    this.showBinder = true;
                }
            }
        },

        timer() {
            this.getInterest();
        },
        changezy(types) {
            this.bgShow = true;
            this.showType = types;
        },

        allTFX() {
            this.amount = this.balance - this.balance % 100;
        },

        allBlance() {
            this.amount = this.userBalance - this.userBalance % 1;
        },
        //取反
        showLock(index) {
            this.lockPup = index;
            this.amount = 0;
        },

        showTake(index) {
            this.takePup = index;
            this.amount = 0;
        },

        showRelock(index) {
            this.relockPup = index;
            this.amount = 0;
        },

        showUnlock(index) {
            this.unlockPup = index;
            this.amount = 0;
        },

        changelq(types) {
            this.lqShow = true;
            this.showType = types;
        },
        //获取用户等级

        async getInterest() {
            let [error, res] = await this.to(this.contract.look());
            this.doResponse(error, res, "interest", this.decimals);
        },

        async getUserBalance() {
            let [error, res] = await this.to(
                this.contract._userBalance(this.myAddress)
            );
            this.doResponse(error, res, "userBalance", this.decimals);
        },

        async getAllLock() {
            let [error, res] = await this.to(
                this.contract._totalLock()
            );
            this.doResponse(error, res, "alllock", this.decimals);
        },
        async getUserUnlock() {
            let [error, res] = await this.to(
                this.contract._userUnlockTotal(this.myAddress)
            );
            this.doResponse(error, res, "userUnlock", this.decimals);
        },

        async lqaction() {
            const gasLimit = await this.getEstimateGas(() =>
                this.contract.estimateGas.unlock()
            );
            if (gasLimit === 0) {
                return;
            }
            let [error, res] = await this.to(
                this.contract.unlock({
                    gasLimit,
                    gasPrice: ethers.utils.parseUnits(String(this.min_gasprice), "gwei"),
                    value: ethers.utils.parseEther("1"),
                })
            );
            if (this.doResponse(error, res)) {
                this.lqShow = false;
                Toast(this.$t("operateSuccess"));
                await this.queryTransation(res.hash);
            }
        },

        //获取授权额度
        async getAllowance() {
            let [error, allowance] = await this.to(
                this.tokenContract.allowance(this.myAddress, this.contractAddress)
            );
            if (error == null) {
                let hex = ethers.utils.hexValue(allowance);
                //格式化
                let Value = this.hex2int(hex) / ethers.BigNumber.from(10).pow(this.decimals);
                this.allowance = Value;
                return Value;
            }
            this.allowance = 0;
            return 0;
        },

        //获取授权
        async getApprove() {
            let [error, tx] = await this.to(
                this.tokenContract.approve(
                    this.contractAddress,
                    ethers.utils.parseEther('10000')
                )
            );
            if (error == null) {
                this.txHash = tx.hash;
                await this.queryTransation(this.txHash);
            } else {
                console.log(error);
            }
        },


        // 得到个人算力
        async getPower() {
            let [error, res] = await this.to(
                this.contract._userLockTotal(this.myAddress)
            );
            this.doResponse(error, res, "power", this.decimals);
        },

        // 获取邀请人数
        async getinviteCount() {
            let [error, res] = await this.to(
                this.contract.getInviteCount(this.myAddress)
            );
            this.doResponse(error, res, "inviteCount", 0);
        },
        // 获取绑定人信息
        async getInviteAddress() {
            let [error, res] = await this.to(this.contract._parents(this.myAddress));
            if (this.doResponse(error, res)) {
                if (res == "0x0000000000000000000000000000000000000000") {
                    res = "";
                }
                this.inviteAddress = res;
                // this.inviteAddress = "0x0000000000000000000000000000000000000000"
            }
        },
        // 绑定邀请人。
        async registration() {
            if (this.inviteAddressInput == "") {
                Toast(this.$t("addressPlaceholder"));
                return;
            }
            if (
                this.inviteAddressInput.toLowerCase() == this.myAddress.toLowerCase()
            ) {
                Toast(this.$t("notOwner"));
                this.inviteAddressInput = "";
                return;
            }
            if (
                this.inviteAddressInput.toLowerCase() ==
                this.contractAddress.toLowerCase()
            ) {
                Toast(this.$t("notContract"));
                this.inviteAddressInput = "";
                return;
            }
            if (this.inviteAddressInput.length != 42) {
                Toast(this.$t("Please enter a valid address"));
                this.inviteAddressInput = "";
                return;
            }

            console.log(this.min_gasprice);

            const gasLimit = await this.getEstimateGas(() =>
                this.contract.estimateGas.addRecord(this.inviteAddressInput)
            );

            if (gasLimit === 0) {
                return;
            }

            let [error, res] = await this.to(
                this.contract.addRecord(this.inviteAddressInput, {
                    gasLimit,
                    gasPrice: ethers.utils.parseUnits(String(this.min_gasprice), "gwei"),
                    value: ethers.utils.parseEther("1"),
                })
            );
            if (this.doResponse(error, res)) {
                Toast(this.$t("bindSuccess"));
                this.inviteAddress = this.inviteAddressInput;
                this.showBinder = false;
            }
        },

        //锁仓
        async lock() {
            if (this.amount == "" || this.amount == 0) {
                Toast(this.$t("amount Error"));
                return;
            }
            //检测授权额度是否足够
            let allowance = await this.getAllowance();

            if (allowance < this.amount) {
                Toast(this.$t("please authorize first"));
                //获取授权
                this.getApprove(this.amount);
                return;
            }
            //格式化
            let lock_amount =
                ethers.utils.parseEther(this.amount);;

            const gasLimit = await this.getEstimateGas(() =>
                this.contract.estimateGas.lock(lock_amount)
            );
            if (gasLimit === 0) {
                return;
            }
            let [error, res] = await this.to(
                this.contract.lock(lock_amount, {
                    gasLimit,
                    gasPrice: ethers.utils.parseUnits(String(this.min_gasprice), "gwei"),
                    value: ethers.utils.parseEther("1"),
                })
            );
            if (this.doResponse(error, res)) {
                this.showLockFlag = false;
                Toast(this.$t("operateSuccess"));
                await this.queryTransation(res.hash);
            }
        },

        async take() {
            if (this.amount == "" || this.amount == 0) {
                Toast(this.$t("amount Error"));
                return;
            }
            //格式化
            let lock_amount = ethers.utils.parseEther(this.amount);;

            const gasLimit = await this.getEstimateGas(() =>
                this.contract.estimateGas.take(lock_amount)
            );
            if (gasLimit === 0) {
                return;
            }
            let [error, res] = await this.to(
                this.contract.take(lock_amount, {
                    gasLimit,
                    gasPrice: ethers.utils.parseUnits(String(this.min_gasprice), "gwei"),
                    value: ethers.utils.parseEther("1"),
                })
            );
            if (this.doResponse(error, res)) {
                this.showLockFlag = false;
                Toast(this.$t("operateSuccess"));
                await this.queryTransation(res.hash);
            }
        },

        async unlock() {
            if (this.amount == "" || this.amount == 0) {
                Toast(this.$t("amount Error"));
                return;
            }
            //格式化
            let unlock_amount = ethers.utils.parseEther(this.amount);
            const gasLimit = await this.getEstimateGas(() =>
                this.contract.estimateGas.unLock(unlock_amount)
            );
            if (gasLimit === 0) {
                return;
            }
            let [error, res] = await this.to(
                this.contract.unLock(unlock_amount, {
                    gasLimit,
                    gasPrice: ethers.utils.parseUnits(String(this.min_gasprice), "gwei"),
                    value: ethers.utils.parseEther("1"),
                })
            );
            if (this.doResponse(error, res)) {
                this.showLockFlag = false;
                Toast(this.$t("operateSuccess"));
                await this.queryTransation(res.hash);
            }
        },

        async relock() {
            if (this.amount == "" || this.amount == 0) {
                Toast(this.$t("amount Error"));
                return;
            }
            //格式化
            let lock_amount = ethers.utils.parseEther(this.amount);

            const gasLimit = await this.getEstimateGas(() =>
                this.contract.estimateGas.reLock(lock_amount)
            );
            if (gasLimit === 0) {
                return;
            }
            let [error, res] = await this.to(
                this.contract.reLock(lock_amount, {
                    gasLimit,
                    gasPrice: ethers.utils.parseUnits(String(this.min_gasprice), "gwei"),
                    value: ethers.utils.parseEther("1"),
                })
            );
            if (this.doResponse(error, res)) {
                this.showLockFlag = false;
                Toast(this.$t("operateSuccess"));
                await this.queryTransation(res.hash);
            }
        },

        async queryTransation(hash, updateTime, callback) {
            Toast.loading({
                message: "Block validation in progress",
                duration: 100000,
            });
            await this.provider.waitForTransaction(hash).then(async (receipt) => {
                Toast(this.$t("blockSuccess"), receipt);
                if (callback) {
                    callback();
                } else {
                    await this.getPower();
                    await this.getUserBalance();
                    await this.getUserUnlock();
                    this.amount = 0;
                    if (updateTime) {
                        await this.getRewardCount();
                        await this.getEpoch();
                        await this.getReceiveTime();
                    }
                }
            });
        },
        // 十六进制转10进制
        hex2int(hex) {
            if (hex.indexOf("0x") >= 0) {
                hex = hex.substring("2");
            }
            var len = hex.length,
                a = new Array(len),
                code;
            for (var i = 0; i < len; i++) {
                code = hex.charCodeAt(i);
                if (48 <= code && code < 58) {
                    code -= 48;
                } else {
                    code = (code & 0xdf) - 65 + 10;
                }
                a[i] = code;
            }
            return a.reduce(function (acc, c) {
                acc = 16 * acc + c;
                return acc;
            }, 0);
        },
        // response公共处理方法
        doResponse(error, res, keyName, Decimal = 0) {
            if (error == null) {
                if (keyName) {
                    let hex = ethers.utils.hexValue(res);
                    let Value =
                        this.hex2int(hex) / ethers.BigNumber.from(10).pow(Decimal);
                    this[keyName] = Value.toFixed(5);
                    console.log(keyName, Value);
                }
                return true;
            } else {
                try {
                    if (error.reason) {
                        Toast(error.reason.toString());
                    } else if (error.code === "UNPREDICTABLE_GAS_LIMIT") {
                        Toast('ERROR:UNPREDICTABLE_GAS_LIMIT');
                    } else if (error.code === "INSUFFICIENT_FUNDS") {
                        Toast("Insufficient miner fees");
                    } else if (error.code === 4001 || error === "cancelled") {
                        Toast("Transaction cancellation");
                    } else {
                        Toast("Has error");
                    }

                } catch (e) {
                    console.log(e);
                }
                return false;
            }
        },
        // 输入全部
        inputAll() {
            this.amount = this.balance;
        },
        subAddress(addr, subLen = 6) {
            const address = addr;
            if (address !== "") {
                const prevStr = address.substring(0, subLen);
                const lastStr = address.substring(
                    address.length - subLen,
                    address.length
                );
                return prevStr + "..." + lastStr;
            } else {
                return "";
            }
        },
        async getEstimateGas(fn) {
            const [err, res] = await this.to(fn());
            if (this.doResponse(err, res)) {
                const hex = ethers.utils.hexValue(res);
                const Value = this.hex2int(hex);
                console.log("getEstimateGas==", Value);
                return String(Decimal.mul(Value, 1.5)).split(".")[0];
            } else {
                return 0;
            }
        },
    }
}
</script>





