<template>
	<div id="app">
		<router-view></router-view>
	</div>
</template>

<script>
export default {
	name: 'app',
	components: {},
	created() {
		window.addEventListener("mousewheel", function (event) {
			if (event.ctrlKey === true || event.metaKey) { event.preventDefault(); }
		}, { passive: false })
	}
}

</script>
